import React, { useState, useMemo } from "react";
import { FiMessageCircle, FiCalendar, FiChevronLeft, FiChevronRight, FiSearch } from "react-icons/fi";
import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";
import "./EventAttendees.css";

interface Attendee {
  name: string;
  tag: string;
  bio: string;
  avatar: string;
  topics: number;
  memberSince: number;
}

interface EventAttendeesProps {
  attendees?: Attendee[];
}

const EventAttendees: React.FC<EventAttendeesProps> = ({ attendees = [] }) => {
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;
  const [currentPage, setCurrentPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState("");
  const [sortOption, setSortOption] = useState("topics");
  const attendeesPerPage = 8;

  const defaultAttendees = [
    { name: "John Doe", tag: "Developer", bio: "Full-stack developer", avatar: "https://randomuser.me/api/portraits/men/1.jpg", topics: 5, memberSince: Date.now() },
    { name: "Jane Smith", tag: "Designer", bio: "UI/UX Designer", avatar: "https://randomuser.me/api/portraits/women/1.jpg", topics: 3, memberSince: Date.now() },
    { name: "Alice Johnson", tag: "Project Manager", bio: "Agile PM", avatar: "https://randomuser.me/api/portraits/women/2.jpg", topics: 8, memberSince: Date.now() },
    { name: "Bob Brown", tag: "Data Scientist", bio: "Machine Learning Expert", avatar: "https://randomuser.me/api/portraits/women/6.jpg", topics: 2, memberSince: Date.now() },
    { name: "Eva Green", tag: "QA Engineer", bio: "Test Automation Specialist", avatar: "https://randomuser.me/api/portraits/women/5.jpg", topics: 6, memberSince: Date.now() },
    { name: "Mike Wilson", tag: "DevOps Engineer", bio: "CI/CD Specialist", avatar: "https://randomuser.me/api/portraits/women/12.jpg", topics: 4, memberSince: Date.now() },
    { name: "Sophie Lee", tag: "Frontend Developer", bio: "React Developer", avatar: "https://randomuser.me/api/portraits/women/10.jpg", topics: 7, memberSince: Date.now() },
    { name: "David Chen", tag: "Backend Developer", bio: "Node.js Developer", avatar: "https://randomuser.me/api/portraits/men/7.jpg", topics: 1, memberSince: Date.now() },
    { name: "Linda Taylor", tag: "Product Owner", bio: "Scrum Product Owner", avatar: "https://randomuser.me/api/portraits/women/8.jpg", topics: 9, memberSince: Date.now() },
    { name: "Tom Johnson", tag: "Mobile Developer", bio: "iOS App Developer", avatar: "https://randomuser.me/api/portraits/women/20.jpg", topics: 10, memberSince: Date.now() }
  ];
  
  const allAttendees = (attendees.length > 0 ? attendees : defaultAttendees).map(attendee => ({
    ...attendee,
    replies: Math.floor(Math.random() * 50) + 5,
    memberSince: new Date(attendee.memberSince).toISOString().split('T')[0]
  }));

  // Filter and sort attendees
  const filteredAndSortedAttendees = useMemo(() => {
    let filtered = allAttendees;
    
    // Apply search filter
    if (searchQuery) {
      filtered = filtered.filter(attendee =>
        attendee.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        attendee.tag.toLowerCase().includes(searchQuery.toLowerCase()) ||
        attendee.bio.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }
    
    // Apply sorting
    const sorted = [...filtered].sort((a, b) => {
      switch (sortOption) {
        case 'titleasc':
          return a.name.localeCompare(b.name);
        case 'titledesc':
          return b.name.localeCompare(a.name);
        case 'new':
          return new Date(b.memberSince).getTime() - new Date(a.memberSince).getTime();
        case 'old':
          return new Date(a.memberSince).getTime() - new Date(b.memberSince).getTime();
        case 'topics':
          return b.topics - a.topics;
        case 'replies':
          return b.replies - a.replies;
        default:
          return 0;
      }
    });
    
    return sorted;
  }, [allAttendees, searchQuery, sortOption]);

  const totalPages = Math.ceil(filteredAndSortedAttendees.length / attendeesPerPage);

  // Get current attendees for the current page
  const indexOfLastAttendee = currentPage * attendeesPerPage;
  const indexOfFirstAttendee = indexOfLastAttendee - attendeesPerPage;
  const currentAttendees = filteredAndSortedAttendees.slice(indexOfFirstAttendee, indexOfLastAttendee);

  // Handle page change
  const handlePageChange = (pageNumber: number) => {
    setCurrentPage(pageNumber);
    document.getElementById('attendees')?.scrollIntoView({ behavior: 'smooth', block: 'start' });
  };

  // Reset to first page when search or sort changes
  React.useEffect(() => {
    setCurrentPage(1);
  }, [searchQuery, sortOption]);

  // Generate page numbers to display
  const getPageNumbers = () => {
    const pageNumbers = [];
    const maxVisiblePages = 5;
    
    if (totalPages <= maxVisiblePages) {
      for (let i = 1; i <= totalPages; i++) {
        pageNumbers.push(i);
      }
    } else {
      const startPage = Math.max(1, currentPage - 2);
      const endPage = Math.min(totalPages, startPage + maxVisiblePages - 1);
      
      for (let i = startPage; i <= endPage; i++) {
        pageNumbers.push(i);
      }
    }
    
    return pageNumbers;
  };

  return (
    <section className="attendees" id="attendees">
      <div className="attendees__container">
        <div className="attendees__header">
          <h2 className="attendees__title">
            <span className="attendees__title-highlight">{content.attendees.heading}</span> / {content.attendees.subheading}
          </h2>
        </div>

        {/* Search and Sort Controls */}
        <div className="attendees__controls">
          <div className="attendees__search">
            <div className="search-bar">
              <FiSearch className="search-bar__icon" />
              <input
                type="text"
                className="search-bar__input"
                placeholder="Search attendees by name, role, or bio..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
          </div>
          
          <div className="attendees__sort">
            <div className="sort-dropdown">
              <select 
                className="sort-dropdown__select"
                value={sortOption}
                onChange={(e) => setSortOption(e.target.value)}
              >
                <option value="titleasc">Order by: Alphabetical ASC</option>
                <option value="titledesc">Order by: Alphabetical DESC</option>
                <option value="new">Order by: New</option>
                <option value="old">Order by: Old</option>
                <option value="topics">Order by: Topics Started</option>
                <option value="replies">Order by: Replies Created</option>
              </select>
            </div>
          </div>
        </div>

        {/* Results Count */}
        <div className="attendees__count">
          Showing {indexOfFirstAttendee + 1}-{Math.min(indexOfLastAttendee, filteredAndSortedAttendees.length)} of {filteredAndSortedAttendees.length} attendees
          {searchQuery && (
            <span className="attendees__search-query"> for "{searchQuery}"</span>
          )}
        </div>

        {/* Attendees Grid */}
        <div className="attendees__grid">
          {currentAttendees.map((attendee, index) => (
            <div key={`${attendee.name}-${index}`} className="attendee-card">
              <div className="attendee-card__avatar">
                <img src={attendee.avatar} alt={attendee.name} />
              </div>
              <span className="attendee-card__tag">{attendee.tag}</span>
              <h3 className="attendee-card__name">{attendee.name}</h3>
              <p className="attendee-card__bio">{attendee.bio}</p>
              <div className="attendee-card__footer">
                <div className="attendee-card__stat">
                  <FiMessageCircle />
                  <span>{attendee.topics} {content.attendees.stats.topics}</span>
                </div>
                <div className="attendee-card__stat">
                  <FiCalendar />
                  <span>{content.attendees.stats.since} {new Date(attendee.memberSince).getFullYear()}</span>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Show empty state if no results */}
        {currentAttendees.length === 0 && (
          <div className="attendees__empty">
            <h3>No attendees found</h3>
            <p>Try adjusting your search criteria or browse all attendees.</p>
          </div>
        )}

        {/* Pagination Controls */}
        {totalPages > 1 && (
          <div className="pagination">
            <button 
              className={`pagination__button pagination__button--prev ${currentPage === 1 ? 'pagination__button--disabled' : ''}`}
              onClick={() => handlePageChange(currentPage - 1)}
              disabled={currentPage === 1}
            >
              <FiChevronLeft />
              Previous
            </button>
            
            <div className="pagination__numbers">
              {getPageNumbers().map(pageNumber => (
                <button
                  key={pageNumber}
                  className={`pagination__number ${currentPage === pageNumber ? 'pagination__number--active' : ''}`}
                  onClick={() => handlePageChange(pageNumber)}
                >
                  {pageNumber}
                </button>
              ))}
              {totalPages > 5 && currentPage < totalPages - 2 && (
                <>
                  <span className="pagination__ellipsis">...</span>
                  <button
                    className="pagination__number"
                    onClick={() => handlePageChange(totalPages)}
                  >
                    {totalPages}
                  </button>
                </>
              )}
            </div>
            
            <button 
              className={`pagination__button pagination__button--next ${currentPage === totalPages ? 'pagination__button--disabled' : ''}`}
              onClick={() => handlePageChange(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              Next
              <FiChevronRight />
            </button>
          </div>
        )}
      </div>
    </section>
  );
};

export default EventAttendees;