import React from "react";
import { FiPlay } from "react-icons/fi";
import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";
import "./EventAbout.css";

interface EventAboutData {
  description: string;
  highlights: string[];
}

interface EventSectionProps {
  data?: EventAboutData;
}

const EventSection: React.FC<EventSectionProps> = ({ data }) => {
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;



  const images = [
    "https://images.unsplash.com/photo-1540575467063-178a50c2df87?q=80&w=600&auto=format&fit=crop",
    "https://images.unsplash.com/photo-1531058020387-3be344556be6?q=80&w=600&auto=format&fit=crop",
    "https://images.unsplash.com/photo-1511578314322-379afb476865?q=80&w=600&auto=format&fit=crop",
    "https://images.unsplash.com/photo-1505373877841-8d25f7d46678?q=80&w=600&auto=format&fit=crop",
  ];

  const handleRegisterClick = () => {
    const pricingSection = document.getElementById("pricing");
    if (pricingSection) pricingSection.scrollIntoView({ behavior: "smooth" });
  };

  const handleWatchVideo = () => {
    alert("üé¨ Video player placeholder ‚Äî will open modal in final version!");
  };

  return (
    <main className="event-page">
     

      {/* Hero Section */}
     

      {/* About Section */}
      <section className="event-about" id="about">
        <div className="event-about__container">
          <div className="event-about__left">
            <h2 className="event-about__title">
              {content.about.title}
            </h2>

            <p className="event-about__description">{data?.description || content.about.description}</p>

            <div className="event-about__buttons">
              <button
                className="event-about__register-btn"
                onClick={handleRegisterClick}
              >
                {content.about.registerButton}
              </button>

              <button
                className="event-about__video-btn"
                onClick={handleWatchVideo}
              >
                <FiPlay size={20} />
                {content.about.videoButton}
              </button>
            </div>
          </div>

          <div className="event-about__right">
            {images.map((image, index) => (
              <div key={index} className="event-about__image-item">
                <img
                  src={image}
                  alt={`Event scene ${index + 1}`}
                  className="event-about__image"
                  loading="lazy"
                />
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Buy Now Section */}
      

    </main>
  );
};

export default EventSection;
