import React, { useState, useEffect } from "react";
import { FiCalendar, FiMapPin, FiUsers } from "react-icons/fi";
import { GrTicket } from "react-icons/gr";
import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";
import "./EventHero.css";

interface EventHeroProps {
  eventName: string;
  eventDate: string;
  location: string;
  remainingTickets: number;
  speakersCount: number;
  bgImage?: string;
  onRegisterClick?: () => void;
}

const  EventHero: React.FC<EventHeroProps> = ({
  eventName,
  eventDate,
  location,
  remainingTickets,
  speakersCount,
  onRegisterClick,
  bgImage,
}) => {
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;

  // Use prop bgImage, or fallback to CMS content bgImage
  const heroImage = bgImage || content.hero.bgImage;

  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });


  useEffect(() => {
    const targetDate = new Date(eventDate).getTime();

    const timer = setInterval(() => {
      const now = new Date().getTime();
      const difference = targetDate - now;

      if (difference > 0) {
        setTimeLeft({
          days: Math.floor(difference / (1000 * 60 * 60 * 24)),
          hours: Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((difference % (1000 * 60)) / 1000),
        });
      } else {
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      }
    }, 1000);

    return () => clearInterval(timer);
  }, [eventDate]);

  const handleRegisterClick = () => {
    if (onRegisterClick) {
      onRegisterClick();
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };

  const formatDateShort = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
    });
  };

  const formatLocationShort = (location: string) => {
    return "Istanbul, TR";
  };



  return (
    <section
      className="event-hero"
      style={{
        backgroundImage: heroImage ? `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${heroImage})` : undefined
      }}
    >
      <div className="event-hero__overlay" />

      <div className="event-hero__inner">
        <div className="event-hero__content">
          <h1 className="event-hero__title">{eventName}</h1>



          <div className="event-hero__countdown">
            <div className="countdown-item">
              <span className="countdown-number">
                {timeLeft.days.toString().padStart(2, '0')}
              </span>
              <span className="countdown-label">Days</span>
            </div>
            <div className="countdown-item">
              <span className="countdown-number">
                {timeLeft.hours.toString().padStart(2, '0')}
              </span>
              <span className="countdown-label">Hours</span>
            </div>
            <div className="countdown-item">
              <span className="countdown-number">
                {timeLeft.minutes.toString().padStart(2, '0')}
              </span>
              <span className="countdown-label">Minutes</span>


            </div>
            <div className="countdown-item">
              <span className="countdown-number">
                {timeLeft.seconds.toString().padStart(2, '0')}
              </span>
              <span className="countdown-label">Seconds</span>
            </div>
          </div>

          <button
            className="event-hero__register-btn"
            onClick={handleRegisterClick}
            aria-label="Register for the event"
          >
            {content.hero.registerButton}
          </button>
        </div>
      </div>

      <div className="event-hero__info-bar">
        <div className="info-item">
          <FiCalendar className="info-icon" aria-hidden="true" />
          <span className="desktop-text">{formatDate(eventDate)}</span>
          <span className="mobile-text">{formatDateShort(eventDate)}</span>
        </div>
        <div className="info-item">
          <FiMapPin className="info-icon" aria-hidden="true" />
          <span className="desktop-text">{location}</span>
          <span className="mobile-text">{formatLocationShort(location)}</span>
        </div>
        <div className="info-item">
          <GrTicket className="info-icon" aria-hidden="true" />
          <span className="desktop-text">
            {remainingTickets} {remainingTickets === 1 ? 'ticket' : 'tickets'} left
          </span>
          <span className="mobile-text">{remainingTickets}</span>
        </div>
        <div className="info-item">
          <FiUsers className="info-icon" aria-hidden="true" />
          <span className="desktop-text">
            {speakersCount} {speakersCount === 1 ? 'speaker' : 'speakers'}
          </span>
          <span className="mobile-text">{speakersCount}</span>
        </div>
      </div>

    </section>
  );
};

export default EventHero;